/* src/App.css */
.App {
  text-align: center;
}

.team-members {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 16px;
}

.team-member {
  background-color: #f0f0f0;
  padding: 16px;
  border-radius: 8px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  width: 240px;
}

.team-member img {
  width: 100%;
  height: auto;
  border-radius: 8px;
  object-fit: cover;
}

.team-member h3 {
  margin: 8px 0;
}

.team-member p {
  margin: 0;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/* src/App.css */
body {
  font-family: Arial, sans-serif;
  background-color: #f7f7f7;
  margin: 0;
  padding: 0;
}

.App {
  text-align: center;
  padding-top: 50px;
}

.team-members {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 16px;
  padding: 30px;
}

.team-member {
  background-color: white;
  padding: 16px;
  border-radius: 8px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  width: 240px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.team-member img {
  width: 120px;
  height: 120px;
  border-radius: 50%;
  object-fit: cover;
  margin-top: -60px;
  border: 5px solid white;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.team-member h3 {
  margin: 8px 0;
  font-size: 20px;
  font-weight: bold;
}

.team-member p {
  margin: 0;
  font-size: 14px;
  color: #666;
}
